<template>
	<div id="login">
		<GoBack></GoBack>
		<div>
			<h1>
				<span @click="changeTip('登录')">zaozuo</span>
				<span @click="changeTip('注册')">造作新家</span>
			</h1>
		</div>
		<div>
			<WrcInput :tip="'账号'" :placeholder="'请输入账号'" v-model="username"></WrcInput>
			<WrcInput :tip="'密码'" :placeholder="'请输入密码'" v-model="password" :type="'password'" v-on:keyup.enter.native="disLogin"></WrcInput>
			{{msg}}
		</div>
		<div>
			<WrcBtn :text="tip" :fontColor="'#fff'" @click="disLogin" :type="'danger'"></WrcBtn>
		</div>
	</div>
</template>

<script>
	import WrcBtn from "../components/wrc-btn/wrc-btn.vue";
	import WrcInput from "../components/wrc-input/wrc-input.vue";
	import GoBack from "../components/go-back/go-back.vue"
	
	export default{
		name:"login",
		components:{
			WrcBtn,
			WrcInput,
			GoBack
		},
		data(){
			return {
				username:"",
				password:"",
				msg:"",
				tip:"登录"
			}
		},
		methods:{
			changeTip(tip){
				this.tip = tip;
			},
			disLogin(e){
				this.tip == "登录" ? this.loginFn(e) : this.registerFn(e);
			},
			registerFn(e){
				this.$axios({
					url:"/register",
					method:"POST",
					data:{
						username:this.username,
						password:this.password
					}
				}).then(
					res=>{
						console.log(res);
						if(res.data.err == "0"){
							this.tip = "登录";
						}else{
							this.msg = res.data.msg
						}
					}
				);
			},
			loginFn(e){
				this.$axios({
					url:"/login",
					method:"POST",
					data:{
						username:this.username,
						password:this.password
					}
				}).then(
					res=>{
						console.log(res);
						if(res.data.err == "0"){
							//种token
							window.localStorage.setItem('user',JSON.stringify(res.data))
							//将token同步到状态管理
							this.$store.commit("login/login",res.data)
							//跳转页面
							this.$router.push("/home")
						}else{
							this.msg = res.data.msg
						}
					}
				);
			},
			submit(){
				console.log(11);
			}
		}
	}
</script>

<style scoped="scoped">
	#login{
		display: flex;
		flex-direction: column;
		/* justify-content: space-evenly; */
		align-items: center;
		background: url(../../public/img/bg2.jpg);
		background-size: cover;
		height: 100%;
	}
	#login div:first-child{
		margin-top: 0.8rem !important;
	}
	#login>div{
		margin-top: 1.6rem !important;
	}
	#login div{
		margin-top: 0.6rem;
		width: 100%;
	}
	#login>div:last-child{
		width: 40%;
	}
	#login>div:nth-of-type(3){
		width: 60%;
		opacity: 0.8;
	}
	#login>div:nth-of-type(4){
		opacity: 0.8;
	}
	#login h1{
		font-size: 0.5rem;
		font-weight: 800;
		display: block;
		text-align: center;
	}
</style>