<template>
	<div>
		<van-icon class="fontStyle" @click="clickFn" :style="{color: colorStyle}" name="arrow-left" />
	</div>
</template>

<script>
	import Vue from 'vue';
	import { Icon } from 'vant';
	
	Vue.use(Icon);
	
	export default{
		name:"go-back",
		props:{
			colorStyle:{
				type:String,
				default:"black"
			}
		},
		methods:{
			clickFn(){
				this.$router.go(-1)
			}
		}
	}
	
</script>

<style scoped="scoped">
	.fontStyle{
		font-size: 0.4rem;
		font-weight: 800;
	}
	div{
		display: flex;
		box-sizing: border-box;
		padding-left: 0.25rem;
		/* margin: 0.25rem 0; */
	}
</style>
