<template>
	<div>
		<GoBack></GoBack>
		<div class="content">
		    <div class="header">
		      <h2><img :src="icon" @click="clickFile" alt=""/></h2>
			  <input type="file" style="display: none;" id="uploadFile"/>
		      <div class="user-box">
		        <a href="login_m.html">昵称:{{nikename}}</a>
		        <a href="javascript:;" v-if="bl" @click="changeMsg">修改</a>
		        <a href="javascript:;" @click="logout">注销</a>
		      </div>
		      <ul class="clear">
		        <li>
		          <span>0</span>
		          <p>关注</p>
		        </li>
		        <li>
		          <span>0</span>
		          <p class="end">粉丝</p>
		        </li>
		      </ul>
		    </div>
		    <div class="docList">
		      <ul>
		        <li class="gk-text">
		          <i></i>
		          <p>公开博文</p>
		          <b></b>
		          <span>0</span>
		        </li>
		        <li class="mm-text">
		          <i></i>
		          <p>秘密博文</p>
		          <b></b>
		          <span>0</span>
		        </li>
		        <li class="cg-text">
		          <i></i>
		          <p>草稿箱</p>
		          <b></b>
		          <span>0</span>
		        </li>
		        <li class="sc-text">
		          <i></i>
		          <p>收藏夹</p>
		          <b></b>
		          <span>0</span>
		        </li>
		        <li class="my_cz">
		          <i></i>
		          <p>收藏夹</p>
		        </li>
		      </ul>
		    </div>
		  </div>
		<AppFooter></AppFooter>
	</div>
</template>

<script>
	import AppFooter from "../components/footer/footer.vue";
	import GoBack from "../components/go-back/go-back.vue";
	export default{
		name:"user",
		data(){
			return {
				icon:"/img/headimg.png",
				nikename:"未登录",
				bl:false
			}
		},
		components:{
			AppFooter,
			GoBack
		},
		methods:{
			logout(){
				window.localStorage.removeItem('user');
				//将token同步到状态管理
				this.$store.commit("login/login",{err:1,msg:"未登录"})
				
				this.getStorage();
			},
			getStorage(){
				let json = window.localStorage.getItem('user');
				json = json==""? "" :JSON.parse(json);
				if(json){
					if(!json.data.icon.includes("http")){
						json.data.icon = this.$baseURL + json.data.icon;
					}
					this.icon = json.data.icon;
					this.nikename = json.data.nikename;
					this.bl = true;
				}else{
					this.icon = "/img/headimg.png";
					this.nikename = "未登录";
					this.bl = false;
				}
			},
			clickFile(){
				document.getElementById("uploadFile").click();
			},
			async changeMsg(){
				console.log("修改头像");
				let json = window.localStorage.getItem('user');
				json = JSON.parse(json);
				//获取上传的图片文件
				let uploadFile = document.querySelector("#uploadFile");
				//处理数据
				let formData = new FormData();
				formData.append("_id",json.data._id);
				formData.append("collectionName","user");
				formData.append("icon", uploadFile.files[0]);
				await this.$axios.post("/user/uploadFile",formData).then(
					res=>{
						console.log(res);
						if(res.data.err == "0"){
							this.icon = this.$baseURL + res.data.icon;
							json.data.icon = this.$baseURL + res.data.icon;
							window.localStorage.setItem('user', JSON.stringify(json));
						}else{
							alert("更换头像失败");
						}
					}
				);
			}
		},
		mounted(){
			this.getStorage();
		},
		updated(){
			this.getStorage();
		}
	}
</script>

<style>
	body{ background:url(../../public/img/bg7.jpg) no-repeat;background-size: cover;}
	  
	.content{max-width:6.4rem; margin:0 auto; margin-bottom:0.8rem;background: lightblue !important;margin-top: 20% !important;}
	.content .header{width:100%;height:2.84rem; background:#9e9a95; display: flex;justify-content: center;}
	.header{padding: 0 !important; padding-top:0.4rem !important;}
	.header h2{width:1.02rem;height:1.02rem; border-radius:50%;overflow: hidden;}
	.header h2 img{width:100%;}
	.header .user-box{font-size:0.25rem; color:#fff; margin-top:0.2rem;display: flex;flex-direction: column;align-items: flex-start;}
	.user-box a{color:red;padding-bottom: 0.2rem; box-sizing: border-box;}
	.header ul{ margin-top:0.4rem;}
	.header ul li{width:50%;height:0.7rem; float:left; color:#fff;}
	.header ul li span{height:0.37rem; line-height:0.37rem; text-align:center; display:block; font-size:0.25rem;}
	.header ul li p{ text-align:center; font-size:0.3rem;height:0.32rem; line-height:0.32rem; border-right:1px solid #fff;}
	.header ul li p.end{ border:0;}

	.content .docList{width:6.4rem; margin:0 auto; margin-top:0.32rem;}
	.docList ul{ border-top:1px solid #7b7c7c;}
	.docList ul li{ background:#fff;height:0.79rem; border-top:1px solid #bcbbba; border-bottom:1px solid #7b7c7c;}
	.docList ul li span{ float:right;margin-right:0.14rem; margin-top:0.26rem;}
	.docList ul li b{width:0.21rem;height:0.24rem; background:url(../assets/images/next_img.png) no-repeat 0 0; background-size:100%; float:right;margin-right:0.34rem; margin-top:0.28rem;}


	.docList ul .gk-text i{width:0.37rem;height:0.37rem; background:url(../assets/images/gk_text.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.29rem; margin-top:0.24rem;}
	.gk-text p{ float:left; font-size:0.25rem;margin-left:0.29rem; margin-top:0.25rem;}
	.docList ul .mm-text i{width:0.29rem;height:0.35rem; background:url(../assets/images/mm_text.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.32rem; margin-top:0.24rem;}
	.mm-text p{ float:left; font-size:0.25rem;margin-left:0.35rem; margin-top:0.25rem;}
	.docList ul .cg-text i{width:0.37rem;height:0.37rem; background:url(../assets/images/cg_text.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.29rem; margin-top:0.24rem;}
	.cg-text p{ float:left; font-size:0.25rem;margin-left:0.29rem; margin-top:0.25rem;}
	.docList ul .sc-text i{width:0.37rem;height:0.37rem; background:url(../assets/images/sc_text.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.29rem; margin-top:0.24rem;}
	.sc-text p{ float:left; font-size:0.25rem;margin-left:0.29rem; margin-top:0.25rem;}
	.docList ul .my_cz{ margin-top:0.45rem;}
	.docList ul .my_cz i{width:0.37rem;height:0.37rem; background:url(../assets/images/my_cz.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.29rem; margin-top:0.24rem;}
	.my_cz p{ float:left; font-size:0.25rem;margin-left:0.29rem; margin-top:0.25rem;}
</style>
