import axios from "axios"
let state = {
	dataArr : []
}

let actions = {
	column:({commit},payload)=>{
		commit("column",payload)
	}
}

let mutations = {
	column:async (state,payload)=>{
		let res = await axios({
			url:"/furniture",
			method:"post",
			data:{
				name:payload.name
			}
		})
		state.dataArr = res.data.data
	}
}

let getters = {
	
}

export default{
	namespaced:true,//命名空间的模块，模块内部直接调用，外部需要命名空间
	state,actions,mutations,getters
}