
let mutations = {
	render:async (state,payload)=>{
		let url = "/"+payload.url;
		let res = await axios({
			url:url,
			method:"post",
			data:{
				name:payload.collectionName
			}
		});
		state.render = res.data.data;
		// return res.data.data;
	},
	changeBl:(state,payload)=>{
		state.bLoading = payload
	}
}

export default mutations;