<template>
	<div>
		<AppHeader :class="'info'"></AppHeader>
		<div class="paddingTop">			
			<!-- 轮播图 -->
			<WrcSwiper :data="data"></WrcSwiper>
			<hr/>
			<!-- 商品 -->
			<Goods></Goods>
		</div>
		<AppFooter></AppFooter>
	</div>
</template>

<script>
	import WrcSwiper from "../wrc-swiper/wrc-swiper.vue";
	import Goods from "../../package/goods.vue";
	import AppHeader from "../header/header.vue";
	import AppFooter from "../footer/footer.vue";
	
	export default{
		name:"wrc-home",
		components:{
			WrcSwiper,
			Goods,
			AppHeader,
			AppFooter,
		},
		data(){
			return {
				data:[
					{_id:1,title:"logo1",sub_title:"xxxxxx1",banner:"/img/logo1.png"},
					{_id:2,title:"logo2",sub_title:"xxxxxx2",banner:"/img/logo2.png"},
					{_id:3,title:"logo3",sub_title:"xxxxxx3",banner:"/img/logo3.png"},
					{_id:4,title:"logo4",sub_title:"xxxxxx4",banner:"/img/logo4.png"}
				]
			}
		},
		mounted(){
			this.$axios({
				url:"/furniture",
				method:"POST",
				data:{
					_limit:4,
					name:"banner"
				}
			}).then(
				res=>{
					if(res.data.err == "0"){
						this.data = res.data.data;
					}
				}
			);
		}
	}
</script>

