<template>
	<div>
		<h3>goods</h3>
		<WrcCell :arr="arr" collectionName="follow"></WrcCell>
	</div>
</template>

<script>
	import WrcCell from "../components/wrc-cell/wrc-cell.vue";
	export default{
		name:"goods",
		data(){
			return {
				arr:[]
			}
		},
		components:{
			WrcCell
		},
		mounted(){
			this.$axios({
				url:"/furniture",
				method:"POST",
				data:{
					name:"follow"
				}
			}).then(
				res=>{
					if(res.data.err == "0"){
						this.arr = res.data.data;
					}
				}
			);
		}
	}
</script>

