import Vue from 'vue'
import App from './App.vue'
import router from "./plugins/router.js";

Vue.config.productionTip = false;

//引入所有全局配置
import "./global";

//引入拦截器
import "./plugins/axios.js";
//导入vuex
import store from "./plugins/vuex.js"

//同步lcoal | vuex
let user = window.localStorage.getItem('user')
let token = user ? JSON.parse(window.localStorage.getItem('user')).token : ''

if(token){
	store.commit("login/login",JSON.parse(user))
}else{
	store.commit("login/login",{err:1,msg:"未登录"})
}

import {baseURL} from "./config";
Vue.prototype.$baseURL = baseURL;

let vm = new Vue({
	data:{
		bLoading:false
	},
	render: h => h(App),
	router,
	store
}).$mount('#app')

export default vm;