import Vue from 'vue'

//1. 引入路由包
import VueRouter from 'vue-router'

//2. 安装插件包到Vue上, 
Vue.use(VueRouter);

import home from "../components/home/home.vue";
import goods from "../package/goods.vue";

// import follow from "../package/follow.vue";
// import column from "../package/column.vue";

// 组件懒加载1
const follow = ()=>import(/* webpackChunkName: "follow" */ "../package/follow.vue");
// const column = ()=>import("../package/column.vue");

import login from "../package/login.vue";
import register from "../package/register.vue";
import user from "../package/user.vue";
import detail from "../package/detail.vue";
import noPage from "../package/noPage.vue";

//3. 路由配置
let routes = [
	{path: '/home',component: home},
	{path: '/goods/:_id',component: detail},
	{
		path: '/goods',
		component: goods,
		children:[
			{
				path:":_id",
				component:detail,
				name:"detail"
			}
		]
	},
	// {path: '/follow',component: follow},
	// {path: '/column',component: column},
	{path: '/follow', component: follow},
	// 组件懒加载2
	{path: '/column', component: (r)=>{require(['../package/column.vue'],r) } },
	
	{path: '/login',component: login},
	{path: '/register',component: register},
	{path: '/user',component: user},
	{path: '/',redirect:"/home"},
	{path: "*", component:noPage}
]

//4.路由实例
let router = new VueRouter({
	 routes,
	 mode:"history"
});

//5.导出路由实例，让他去控制vue根
export default router