<template>
	<div id="wrc-input">
		<span :class="classes">{{tip}}</span>
		<input :type="type" :value="value" @input="$emit('input',$event.target.value)" :placeholder="placeholder" :maxlength="maxlength" :class="classes"/>
	</div>
</template>

<script>
	export default{
		name:"wrc-input",
		props:{
			tip:{
				type:String,
				default:"标题"
			},
			type:{
				type:String,
				default:"text"
			},
			placeholder:{
				type:String,
				default:"请输入内容"
			},
			maxlength:{
				type:Number,
				default:16
			},
			classes:{
				type:String,
				default:"info"
			},
			value:{
				type:String,
				default:"username"
			}
		}
	}
</script>

<style scoped="scoped">
	#wrc-input{
		display: flex;
		border-radius: .2rem;
		overflow: hidden;
	}
	#wrc-input span{
		display: inline-block;
		width: 30%;
		line-height: 0.8rem;
		font-size: 0.2rem;
		border-right: 1px solid #FFF;
	}
	#wrc-input input{
		box-sizing: border-box;
		padding-left: 0.4rem;
		display: inline-block;
		width: 70%;
		line-height: 0.8rem;
		font-size: 0.2rem;
		outline: none;
		border: none;
	}
	.info{
		background: lightblue;
	}
	.danger{
		background: lightcoral;
	}
</style>
